.game-table .ant-table-small {
  border-left: 0;
  background: #fff; }
  .game-table .ant-table-small > .ant-table-content > .ant-table-body > table {
    padding: 0px; }

.game-table .ant-table-middle > .ant-table-content > .ant-table-scroll > .ant-table-header tr:last-child th {
  padding: 6px 16px; }

.game-table thead tr:first-child {
  display: none; }

.game-table th, .game-table td {
  text-align: center !important; }

.game-table .editable-cell-value-wrap {
  height: 24px;
  cursor: pointer; }

.game-table tr {
  transition: outline-color .7s ease-out; }
  .game-table tr.failed-check {
    outline: 1px solid red;
    outline-offset: -1px; }

.game-table td .ant-form-item-control {
  line-height: inherit; }

.game-table td.failed-check {
  background: #ffc8c8 !important; }

.game-layout .ant-layout, .game-layout.ant-layout {
  background: none; }

.game-layout .ant-menu {
