  min-width: 65px;
  float: right;
  position: relative;
  transition: all 0.3s; }
  .game-layout .ant-menu .ant-menu-item {
    margin: 0;
    line-height: 36px;
    height: 36px;
    border-bottom: 1px solid #e8e8e8; }
    .game-layout .ant-menu .ant-menu-item:not(:last-child) {
      margin: 0; }

.game-layout .ant-card.card {
  border-radius: 0;
  display: inline-block;
  width: 200px;
  height: 80px;
  text-align: center;
  overflow: hidden;
  cursor: move; }

.game-layout .ant-card-head {
  min-height: 0;
  padding: 0; }

.game-layout .ant-card-head-title {
  padding: 8px 0; }

.game-layout .ant-card-body {
  padding: 8px 0; }

.game-layout .ant-table-small > .ant-table-content > .ant-table-body {
  margin: 0px; }

.game-layout .ant-table-small .ant-table-row td {
  height: 36px;
  line-height: 1; }

.game-layout span.card-btn {
  background: url(/src/images/spades.svg) no-repeat center;
  background-size: contain;
  display: inline-block;
  height: 52px;
  width: 40px; }

.mobile .game-table {
  text-align: center;
  padding-top: 8px; }
  .mobile .game-table .game-header {
    text-align: left;
    padding-left: 6px; }
  .mobile .game-table .table-row:not(:last-child) .table-cell {
    border-bottom: 1px solid #e8e8e8; }
  .mobile .game-table .table-row:last-child .table-cell {
    border-top: 1px solid #e8e8e8; }
  .mobile .game-table .table-row.current-round > div:first-child {
    border-right: 2px solid #1a911a; }
    .mobile .game-table .table-row.current-round > div:first-child .table-cell {
      background: #c7d1c5;
      color: #1a911a; }
  .mobile .game-table .table-row.round-fell:before {
    content: ' ';
