  .mobile .game-table .table-row.round-fell .score-cell {
    text-decoration: line-through; }
  .mobile .game-table .table-cell {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
    padding: 4px;
    height: 30px; }
    .mobile .game-table .table-cell.fell {
      color: red; }
    .mobile .game-table .table-cell.highest-bidder {
      background: #f8f8f8; }
    .mobile .game-table .table-cell .trump {
      position: absolute;
      right: 4px;
