.mobile .dial-pad {
  min-width: auto; }

.mobile .item2 {
  margin-bottom: 10px; }

.mobile .game-mobile-view {
  height: 100%;
  display: flex;
  flex-direction: column; }
  .mobile .game-mobile-view .round-navigation {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end; }
    .mobile .game-mobile-view .round-navigation button {
      border: none;
      box-shadow: none; }
      .mobile .game-mobile-view .round-navigation button.left {
        margin-right: auto; }

.game-pad-container {
  display: grid;
  width: 100%;
  height: 100%;
  max-width: 980px;
  grid-auto-rows: auto;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr auto 1fr;
  justify-items: center;
  margin-top: 20px; }
  .game-pad-container .item2,
  .game-pad-container .item4,
  .game-pad-container .item6,
  .game-pad-container .item8 {
    align-self: center; }
    .game-pad-container .item2 .ant-radio-button-wrapper,
    .game-pad-container .item4 .ant-radio-button-wrapper,
    .game-pad-container .item6 .ant-radio-button-wrapper,
    .game-pad-container .item8 .ant-radio-button-wrapper {
      height: auto; }
  .game-pad-container .item4 {
    justify-self: end;
    margin-right: 52px; }
  .game-pad-container .item6 {
    justify-self: left;
    margin-left: 50px; }
  .game-pad-container .item8 {
    grid-column-start: 2;
    grid-column-end: 3;
    transform: translateY(-15px); }
  .mobile .game-pad-container {
    margin-top: 0px;
    padding-top: 8px;
    display: block; }
    .mobile .game-pad-container h3 {
      margin-bottom: 12px;
      padding-left: 6px; }
    .mobile .game-pad-container .item2,
    .mobile .game-pad-container .item4,
    .mobile .game-pad-container .item6 {
      align-self: center; }
    .mobile .game-pad-container .item4 {
      margin-right: 0;
      justify-self: auto; }
    .mobile .game-pad-container .item6 {
      justify-self: auto;
      margin-left: 0; }
    .mobile .game-pad-container .item8 {
      transform: translateY(0px);
      margin-top: 12px; }
    .mobile .game-pad-container .cards-pad {
      grid-gap: 4px; }
    .mobile .game-pad-container .dial-pad {
      padding: 6px;
      grid-gap: 16px 8px; }
  @media screen and (max-width: 360px) {
    .game-pad-container label {
      padding: 0 10px; } }

.my-carousel {
  overflow: hidden; }
  .my-carousel .my-carousel-slides-container {
    display: flex;
    width: 100%;
    transition: all 0.3s ease-in; }
    .my-carousel .my-carousel-slides-container.panel {
      transform: translateX(-100%); }
  .my-carousel .my-carousel-slide {
    width: 100%;
    min-width: 100%; }

@keyframes bounce-right {
  0%, 100% {
    transform: translateX(0); }
  60% {
    transform: translateX(20px); } }

.bounce-right {
  animation-duration: 0.4s;
  animation-name: bounce-right; }

@keyframes bounce-left {
  0%, 100% {
    transform: translateX(0); }
  60% {
    transform: translateX(-20px); } }

.bounce-left {
  animation-duration: 0.4s;
  animation-name: bounce-left; }

@keyframes slide-up {
  0% {
    transform: translateY(30px); }
  100% {
    transform: translateY(0); } }

.slide-up {
  animation-duration: 0.4s;
  animation-name: slide-up; }

@keyframes fade-in {
  0% {
    transform: scale(0);
    opacity: 0; }
  100% {
    transform: scale(1);
    opacity: 1; } }

.fade-in {
  animation-duration: 0.4s;
  animation-name: fade-in; }
