.btn-menu-arrow {
  width: 18px; }
  .btn-menu-arrow.right {
    height: 14px;
    transform: rotate(0deg);
    transition: transform .5s ease; }
    .btn-menu-arrow.right > span {
      display: inline-block;
      width: 100%;
      height: 2px;
      position: relative;
      top: -7px;
      background: #0e6b11; }
      .btn-menu-arrow.right > span:before, .btn-menu-arrow.right > span:after {
        content: '';
        display: inline-block;
        width: 18px;
        height: 2px;
        background-color: inherit;
        position: absolute;
        right: 0; }
      .btn-menu-arrow.right > span:before {
        top: -5px;
        transition: transform .3s ease;
        transform-origin: 0; }
      .btn-menu-arrow.right > span:after {
        bottom: -5px;
        transition: transform .3s ease, width .25s ease .25s;
        transform-origin: 0; }
    .btn-menu-arrow.right.spin {
      transform: rotate(360deg); }
    .btn-menu-arrow.right.arrow span:before {
      right: -4px;
      top: -4px;
      transform: rotate(45deg) scaleX(0.6); }
    .btn-menu-arrow.right.arrow span:after {
      right: -4px;
      bottom: -4px;
      transform: rotate(-45deg) scaleX(0.6); }
  .btn-menu-arrow.left {
    height: 14px;
    transform: rotate(0deg);
    transition: transform .5s ease; }
    .btn-menu-arrow.left > span {
      display: inline-block;
      width: 100%;
      height: 2px;
      position: relative;
      top: -7px;
      background: #0e6b11; }
      .btn-menu-arrow.left > span:before, .btn-menu-arrow.left > span:after {
        content: '';
        display: inline-block;
        width: 18px;
        height: 2px;
        background-color: inherit;
        position: absolute;
        right: 0; }
      .btn-menu-arrow.left > span:before {
        top: -5px;
        transition: transform .3s ease;
        transform-origin: 9px; }
      .btn-menu-arrow.left > span:after {
        bottom: -5px;
        transition: transform .3s ease, width .25s ease .25s;
        transform-origin: 9px; }
    .btn-menu-arrow.left.spin {
      transform: rotate(-360deg); }
    .btn-menu-arrow.left.arrow span:before {
      left: -4px;
      top: -4px;
      transform: rotate(-45deg) scaleX(0.6); }
    .btn-menu-arrow.left.arrow span:after {
      left: -4px;
      bottom: -4px;
      transform: rotate(45deg) scaleX(0.6); }

.btn-menu-arrow-container {
  width: 32px;
  margin: 10px 0;
  padding: 6px 7px 8px;
  cursor: pointer; }
