.clickable-list {
  padding: 0;
  margin: 0;
  list-style: none;
  border: 1px solid #e8e8e8; }
  .clickable-list li {
    padding: 5px;
    cursor: pointer;
    transition: all .5s ease-out; }
    .clickable-list li:not(:first-child) {
      border-top: 1px solid #e8e8e8; }
    .clickable-list li:hover {
      color: #fff;
      background: #1890ff;
      padding-left: 9px; }
    .clickable-list li:active {
      background: #096dd9; }
