.league-container {
  background: transparent;
  height: 100%; }
  .league-container .ant-layout {
    background: transparent; }
  .league-container .btn-menu-arrow-container {
    float: right; }
  .league-container .rotate-180 {
    transform: rotate(180deg); }
  .league-container .ant-menu-inline-collapsed .ant-menu-item > a {
    max-width: 0;
    display: inline-block;
    opacity: 0; }

.league-menu .ant-menu-item > a {
  display: inline;
  transition: opacity 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), width 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  opacity: 1; }
